*{
  margin:0
}

.app{
  background-color: #fafafa;
  height: 100vh;

}
.app_posts{
  padding: 20px;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.app_header{
  background-color: white;
  padding: 20px;
  border-bottom: 1px solid lightgrey;
  display: flex;
  justify-content: space-between;
}
.app_headerImage{
  object-fit: contain;

}

.app_signup{
  display: flex;
  flex-direction: column;
}