
.post{
    margin-bottom: 45px;
    max-width: 400px;
    background-color: white;
    border:1px solid lightgrey;
}
.post_image{
    
    object-fit: contain;
    width:100%;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;


}
.post_avatar{
    margin-right:5px
}

.post_text{
    font-weight: normal;
    padding:20px;
}

.post_header{
    display: flex;
    align-items: center;
    padding:20px;
}